<template>
  <div class="wrapper-dropdown">
	  <span @click="toggleDropdown()" v-html="selector"></span>
	<ul class="dropdown" v-show="active">
		<li v-for="color in colors" :key=color.hex @click="setColor(color.hex, color.name)"><span :style="{background: color.hex}"></span> {{color.name}}</li>
	</ul>
</div>
</template>

<script>
export default {
	data() {
		return {
		active: false,
		selectedColor: '',
		selectedColorName: '',
		colors: [
			{
				hex: '#00759A',
				name: "Bleu"
			},
			{
				hex: '#F7941D',
				name: 'Orange'
			},
			{
				hex: '#A71930',
				name: 'Rouge'
			},
			{
				hex: '#679146',
				name: 'Vert'
			}
		]
		}
	},
	computed: {
		selector: function() {
			if(!this.selectedColor) {
				return 'Choisir...';
			}
			else {
				return '<span style="background: ' + this.selectedColor + '"></span> ' + this.selectedColorName;
			}
		}
	},
	methods: {
		setColor: function(color, colorName) {
			this.selectedColor = color;
			this.selectedColorName = colorName;
			this.active = false;
		},
		toggleDropdown: function() {
			this.active = !this.active;
		},
	}
	
}
</script>

